.top-paid-modal {
  display: flex;
  justify-content: flex-start;
  color: #312b2b;
  font-weight: 600;
  margin-bottom: 15px;
}

.top-paid-modal > div {
  width: 33%;
}

.top-paid-modal > div:first-child {
  padding-right: 45px;
  div {
    display: flex;
    justify-content: space-between;
    margin-bottom: 15px;
  }
  div:nth-child(n+2){
    margin-bottom: 4px;
  }
}

.input-money {
  width: 150px !important;
  .ant-input-number-input {
    border-bottom: 2px solid #aca5a5 !important;
  }
  .ant-input-number-handler-wrap {
    display: none !important;
  }
}

.top-paid-modal > div:nth-child(n + 2) {
  display: flex;
  justify-content: space-between;
  padding-right: 45px;
  div {
    span:first-child {
      margin-right: 10px;
    }
  }
}

.middle-paid-modal {
  margin-bottom: 15px;
  color: #312b2b;
  font-weight: 600;
  .total-money{
      margin-top: 10px;
      display: flex;
      flex-direction: column;
       align-items: flex-end;
      div{
          width: 300px;
          display: flex;
          justify-content: space-between;
      }
  }
}

.bottom-paid-modal {
  display: flex;
  justify-content: flex-end;

  button {
    height: 40px;
    margin-right: 10px;
    border: none;
    border-radius: 4px;
  }

  button:nth-child(n + 1) {
    background-color: #419543;
  }
  button:nth-child(n + 2) {
    background-color: #419543;
  }
  button:nth-child(n + 3) {
    background-color: #898c8d;
    margin-right: 0;
  }
  button:nth-child(n + 1):hover {
    background-color: #2b612c;
  }
  button:nth-child(n + 2):hover {
    background-color: #2b612c;
  }
  button:nth-child(n + 3):hover {
    background-color: #494d4e;
  }
}

h6{
  position: relative;
  bottom: 15px;
  text-align: center;
  color: rgb(199, 55, 55)!important;
  font-size: 11px;
}

.main-adjust-modal{
  color: #312b2b;
  font-weight: 600;
  div{
    margin-bottom: 15px;
    display: flex;
    justify-content: space-between;
    span:first-child{
      width: 220px !important;
    }
    span:last-child,div{
      width: 100%;
      
    }
  }
 
  div:nth-child(n+2){
    margin-bottom: 10px;
    .time-action{
      border-bottom: 2px solid #aca5a5 !important;
      span:last-child{
        position: relative;
        right: 55px;
      }
    }
  }
  
  div:nth-child(n+3){
    margin-bottom: 15px;
  }

  .input-money-debt {
    .ant-input-number-input {
      border-bottom: 2px solid #aca5a5 !important;
    }
    .ant-input-number-handler-wrap {
      display: none !important;
    }
  }
  .input-money-note{
    border-bottom: 2px solid #aca5a5 !important;
  }

  
}

.bottom-adjust-modal{
  display: flex;
  justify-content: flex-end;
  button {
    height: 40px;
    margin-right: 10px;
    border: none;
    border-radius: 4px;
  }

  button:nth-child(n + 1) {
    background-color: #419543;
  }

  button:nth-child(n + 2) {
    background-color: #898c8d;
    margin-right: 0;
  }

  button:nth-child(n + 1):hover {
    background-color: #2b612c;
  }
  button:nth-child(n + 2):hover {
    background-color: #494d4e;
  }
}
