$res_320: "only screen and (min-width: 320px) and (max-width: 424px)";
// $res_375 : "only screen and (min-width: 375px) and (max-width: 424px)";
// $res_425 : "only screen and (min-width: 425px) and (max-width: 767px)";
// $res_768 : "only screen and (min-width: 768px) and (max-width: 991px)";
// $res_992 : "only screen and (min-width: 992px) and (max-width: 1023px)";
// $res_1024 : "only screen and (min-width: 1024px) and (max-width: 1999px)";
// $res_1200 : "only screen and (min-width: 1200px)";
@mixin responsive_320() {
  @media #{$res_320} {
    @content;
  }
}
.partner-wrapper {
  .btn {
    width: 10%;
    margin: 0 auto;
    .btn-add__partner {
      background-color: #4bac4d;
      color: white;
      border-radius: 4px;
    }
  }
  @include responsive_320() {
    .btn {
      display: flex;
      justify-content: center;
      margin-top: 3px;
    }
  }
  .icon-plus__edit {
    color: white;
  }
}
.partner-table__mobile {
  display: none;
}

.partner-table__pc {
  position: relative;
  bottom: 50px;
  width: 100%;
  padding-right: 35px;
  padding-left: 35px;

  h2 {
    position: relative;
    top: 40px;
  }
  .top-table-list-partner {
    display: flex;
    justify-content: flex-end;

    button {
      height: 40px;
      margin-right: 10px;
      border: none;
      border-radius: 4px;
    }

    button:nth-child(n + 1) {
      background-color: #419543;
    }

    button:nth-child(n + 2) {
      background-color: #898c8d;
      margin-right: 0;
    }
    button:nth-child(n + 1):hover {
      background-color: #2b612c;
    }
    button:nth-child(n + 2):hover {
      background-color: #494d4e;
    }
  }

  .table-list-partner {
    margin-top: 10px;
    .ant-table-thead > tr > th {
      background-color: #dcf4fc;
    }
  }
}

@include responsive_320() {
  .partner-table__pc {
    display: none;
  }
  .partner-table__mobile {
    display: block;
    .total-partner {
      margin-top: 5px;
      margin-left: 15px;
      span {
        font-size: 12px;
      }
    }
    .partner-info {
      display: flex;
      justify-content: space-around;
      margin-top: 10px;
      border-bottom: 1px solid #eee;
      padding-bottom: 10px;
      .partner-info__avatar {
        margin-left: 0px;
        margin-top: 5px;
      }
      .partner-info__info {
        margin-left: -5px;
        padding-left: 0;
        width: 165px;
        .name {
          color: rgb(27, 26, 26);
          font-weight: 600;
          font-size: 14px;
          overflow: hidden;
          width: 165px;
          display: -webkit-box;
          -webkit-line-clamp: 1;
          -webkit-box-orient: vertical;
        }
        .code {
          color: rgb(138, 127, 127);
          font-size: 13px;
        }
        .phone {
          color: rgb(126, 112, 112);
          font-size: 13px;
        }
      }
      .partner-info__money {
        width: 70px;
        text-align: right;
      }
    }
  }
}
